import { Action } from '@ali/4ever-cangjie';
import { ITableSelection } from '../types';
import Table from '../../mo/models';

export const TABLE_SET_CELL_VALIGN = Symbol('TABLE_SET_CELL_VALIGN');

export function setTableCellVAlign(table, selection, vAlign) {
  return {
    type: TABLE_SET_CELL_VALIGN,
    payload: {
      node: table,
      selection,
      vAlign,
    },
  };
}

interface TableSetTableCellVAlignPayload {
  node: Table;
  selection: ITableSelection;
  vAlign: Record<string, any>;
}

interface TableSetTableCellVAlignAction {
  type: typeof TABLE_SET_CELL_VALIGN;
  payload: TableSetTableCellVAlignPayload;
}

export function isTableSetTableCellVAlignAction(
  action: Action,
): action is TableSetTableCellVAlignAction {
  return action.type === TABLE_SET_CELL_VALIGN;
}
